import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DXComponentsModule } from 'src/app/shared/dx-components.module';
import { CustomButtonDirective } from '../../directive/form-controls/custom-button.directive';

@Component({
  selector: 'app-html-content-viewer',
  standalone: true,
  imports: [DXComponentsModule, CustomButtonDirective],
  templateUrl: './html-content-viewer.component.html',
  styleUrls: ['./html-content-viewer.component.scss']
})
export class HtmlContentViewerComponent {
  @Input() htmlContent: string
  @Input() isHTMLContentVisible: boolean
  @Output() isHTMLContentVisibleChange = new EventEmitter()
}