import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DXComponentsModule } from 'src/app/shared/dx-components.module';
import { CustomButtonDirective } from '../../directive/form-controls/custom-button.directive';

@Component({
  selector: 'app-text-content-viewer',
  imports: [DXComponentsModule, CustomButtonDirective],
  templateUrl: './text-content-viewer.component.html',
  standalone: true,
  styleUrls: ['./text-content-viewer.component.scss']
})
export class TextContentViewerComponent {
  @Input() textTitle: string = 'Text內容'
  @Input() textContent: string
  @Input() isTextContentVisible: boolean
  @Output() isTextContentVisibleChange = new EventEmitter()
}