import { Directive, Input, OnInit } from '@angular/core';
import { DxFormComponent } from 'devextreme-angular/ui/form';
import { LocalizationService } from '@abp/ng.core';
import { cloneDeep } from "lodash";
import type { InitializedEvent, Properties } from 'devextreme/ui/form';
import { DataFieldLookUpSourceMapping, DataFieldTranslationKeysMapping, FormItemTranslationMapping } from '../../typing';
import { LookupDataSourceService } from '../../service/lookup-data-source.service';
import { basiFormProperties } from './const';
import { FormTenantService } from './tenant.service';
import { TenantChooserService } from '../../modules/tenant-chooser/tenant-chooser.service';


@Directive({
  selector: '[appFormFeature]',
  standalone: true,
  providers: [FormTenantService]
})
/**
 * A directive that setup basic configurations.
 */
export class FormFeatureDirective implements OnInit {
  @Input() dataFieldLookUpSourceMapping: DataFieldLookUpSourceMapping
  @Input() formItemTranslationMapping: FormItemTranslationMapping;
  @Input() dataFieldTranslationKeysMapping: DataFieldTranslationKeysMapping
  @Input() formProperties?: Properties;
  lang: string;
  isChinese: boolean;
  constructor(
    private form: DxFormComponent,
    private localization: LocalizationService,
    private lookupDataSourceService: LookupDataSourceService,
    private tenantService: FormTenantService
  ) {
    for (const key in basiFormProperties) {
      this.form[key] = basiFormProperties[key]
    }


    this.form.customizeItem = e => {
      //   if (e.cssClass) {
      //     e.cssClass = `webHR__formItem ${e.cssClass}`
      //   } else {
      //     e.cssClass = 'webHR__formItem'
      //   }
      this.formDataFieldLocalization(e)
      this.editorOptionPreProcess(e)
      this.patchLookupDataSource(e)
    }
  }

  ngOnInit(): void {

    this.tenantService.tenantProcess(this.form, this.dataFieldLookUpSourceMapping)

    this.lang = this.localization.currentLang
    if (this.lang.includes('zh')) {
      this.isChinese = true;
    } else {
      this.isChinese = false;
    }
    if (this.formProperties) {
      const formPropertyKeys = Object.keys(this.formProperties);
      formPropertyKeys.forEach(key => {
        this.form[key] = this.formProperties[key]
      })
    }
    // Since the object will be affected by the translation process, use a cloned one to do it,
    // then every time the object will be the same.
    this.form.instance.on('initialized', (e) => {
      const items = this.form.instance.option('items')
      const _items = cloneDeep(items);
      // this.form.instance.option({items: _items})
      this.form.instance.option('items', _items)
      this.formItemLocalization(e)
    })
  }

  /**
   * Translate form related word like tabbed title by path.
   * https://supportcenter.devexpress.com/ticket/details/t753947/how-to-get-instance-of-edit-form-in-dxdatagrid-in-order-to-disable-tabs
   * https://js.devexpress.com/Angular/Documentation/ApiReference/UI_Components/dxForm/Methods/#itemOptionid_option_value
  */
  formDataFieldLocalization(e) {
    const dataField = e.dataField;
    const translationKeyArray = this.dataFieldTranslationKeysMapping?.find(kM => kM.dataField === dataField)?.translateKeys
    if (translationKeyArray) {
      const translatedCaptionArray = translationKeyArray.map((tK) => {
        return this.localization.instant(`HRM::${tK}`);
      })
      const translatedCaption = translatedCaptionArray?.join(this.isChinese ? '' : ' ')
      e.label = { ...e.label, text: translatedCaption }
      e.title = translatedCaption
      e.caption = translatedCaption
    }
    e.validationRules?.forEach(vR => {
      vR.message = this.localization.instant(vR.message)
    })
  }

  formItemLocalization(e: InitializedEvent) {
    this.formItemTranslationMapping?.forEach(m => {
      const translatedCaptionArray = m.translateKeys.map((tK) => {
        return this.localization.instant(`HRM::${tK}`);
      })
      const translatedCaption = translatedCaptionArray?.join(this.isChinese ? '' : ' ')
      e.component.instance().itemOption(m.itemPath, {
        title: translatedCaption,
        caption: translatedCaption
      });
    })
  }

  /**
   * Get columns with lookup datasource set using lookUpDataFieldAndSourceMapping.
   * @param {Column[]} columns Column of DxDatagrid
   * @returns {Column[]}
   */
  patchLookupDataSource(e) {

    const lookUpDataSourceMap = this.dataFieldLookUpSourceMapping?.find(m => {
      return m.dataField === e.dataField;
    })
    if (lookUpDataSourceMap) {
      const lookUpDataSource = this.lookupDataSourceService.getLookupDataSource(
        lookUpDataSourceMap,
        {
          form: this.form
        }
      )
      e.editorOptions = {
        ...e.editorOptions,
        dataSource: lookUpDataSource,
        valueExpr: 'id',
        displayExpr: 'displayName'
      }
    }
  }

  /**
  * Adjust editorOptions in each column.
  * Iterate throuth each column to adjust them.
  * @param {Column[]} columns Column of DxDatagrid
  * @returns {Column[]}
  */
  editorOptionPreProcess = (e) => {
    if (e.editorType === 'dxSelectBox') {
      //   const editorOptions = this.devComponentPropService.lookupEditorOptions
      e.editorOptions = {
        ...e.editorOptions,
        searchEnabled: true,
        placeholder: '請選擇...'
      }
    }

    if (e.editorType === 'dxDateBox') {
      //   const editorOptions = this.devComponentPropService.dateEditorOptions
      e.editorOptions = {
        ...e.editorOptions
      }
    }

    e.editorOptions = {
      ...e.editorOptions,
      validationMessageMode: 'always'
    }
  }
}