import { Injectable } from "@angular/core";
import { DxDataGridComponent, DxFormComponent } from "devextreme-angular";
import { TenantChooserService } from "../../modules/tenant-chooser/tenant-chooser.service";
import { Column, EditorPreparingEvent, InitNewRowEvent } from "devextreme/ui/data_grid";
import { FocusInEvent } from "devextreme/ui/select_box";
import { FieldDataChangedEvent } from "devextreme/ui/form";
import { DataFieldLookUpSourceMapping } from "../../typing";

@Injectable()
export class FormTenantService {
    constructor(
        private tenantChooserService: TenantChooserService,
    ) {}
    /**
     * [DxDataGridComponent]{@link https://js.devexpress.com/Angular/Documentation/ApiReference/UI_Components/dxDataGrid/}
     * 
     * 如果表單中有SpecifiedTenantId這個欄位，則於此欄位變更時，清除其他欄位資料。
     * 
     * 新增時，如果全域的租戶選擇器不是不指定，也不是host則，將值帶入。
     * 
     * @param grid 
     */
    tenantProcess(form: DxFormComponent, dataFieldLookUpSourceMapping: DataFieldLookUpSourceMapping) {
        this.specifiedTenantProcess(form);
        this.clearFormIfSpecifiedTenantIdChanges(form, dataFieldLookUpSourceMapping);
        this.customizeItem(form);
        // this.gridNewRow(grid);
        // this.dataSourceChangeForSpecifiedTenantId(grid)
    }
    /**
     * [DxDataGridComponent]{@link https://js.devexpress.com/Angular/Documentation/ApiReference/UI_Components/dxDataGrid/}
     * 
     * 新增時，如果全域的租戶選擇器不是不指定，也不是host則將值帶入。
     * 
     * @param grid 
     */
    gridNewRow(grid: DxDataGridComponent){
        grid.instance.on("initNewRow", (e: InitNewRowEvent) => {
            const tenandId = this.tenantChooserService.tenantId.getValue()
            if (tenandId !== '' && tenandId !== 'null') {
                e.data['SpecifiedTenantId'] = tenandId
            }
        })
    }
    /**
     * [DxForm]{@link https://js.devexpress.com/Angular/Documentation/ApiReference/UI_Components/dxForm}
     * 
     * 如果表單中有SpecifiedTenantId這個欄位，則於此欄位變更時，清除其他欄位資料。
     *
     * 新增時，如果全域的租戶選擇器不是不指定，也不是host則將SpecifiedTenantId設為不可變更。
     * @param form
     */
    specifiedTenantProcess = (form: DxFormComponent) => {
        const customizeItem = form.customizeItem;
        const tenantId = this.tenantChooserService.tenantId.getValue()

        form.instance.on('contentReady', () => {
            if (tenantId) {
                form.instance.updateData({ "SpecifiedTenantId": tenantId })
                const editor = form.instance.getEditor('SpecifiedTenantId')
                editor.option("disabled", true);
            }
        })
    }
    /**
     * [FieldDataChangedEvent]{@link https://js.devexpress.com/Angular/Documentation/ApiReference/UI_Components/dxForm/Events/#fieldDataChanged}
     * 
     * 租戶改變時，清空有選單的控制項。
     * 
     * 若原為陣列者，設定為空陣列。
     * @param form 
     */
    clearFormIfSpecifiedTenantIdChanges(form: DxFormComponent, dataFieldLookUpSourceMapping: DataFieldLookUpSourceMapping) {
        (window as any).formComponent = form;
        form.instance.on('fieldDataChanged', (e: FieldDataChangedEvent) => {
            if (e.dataField === 'SpecifiedTenantId') {
                const formData = form.instance.option("formData")
                const formDataKeys = Object.keys(formData)
                const editors = formDataKeys.map(k => {
                    return {
                        dataField: k,
                        instance: form.instance.getEditor(k)
                    };
                })
                const newFormData = {}
                editors.forEach(editor => {
                    const isLookUp = dataFieldLookUpSourceMapping.find(d => d.dataField === editor.dataField)
                    if (
                        isLookUp &&
                        editor.dataField !== 'SpecifiedTenantId'
                    ) {
                        const isValueArray = Array.isArray(formData[editor.dataField]);
                        newFormData[editor.dataField] = isValueArray ? []: null;
                    }
                })

                form.instance.updateData(newFormData)
            }
        })
    }
    /**
     * [DxFormComponent]{@link https://js.devexpress.com/Angular/Documentation/Guide/UI_Components/Form/Overview/}
     * 
     * 當SpecifiedTenantId存在於表單內時，且該欄位值變化時，重設選單的dataSource
     * 
     * @param form DxFormComponent 
     */
    customizeItem = (form: DxFormComponent) => {
        const customizeItem = form.customizeItem;
        form.customizeItem = (e) => {
            customizeItem?.(e);
            e['editorOptions'] = {
                ...e['editorOptions'],
                onFocusIn: (focusInEvent: FocusInEvent)=> {
                    const specifiedTenantIdEditor = form.instance.getEditor("SpecifiedTenantId")
                    if (specifiedTenantIdEditor && e.dataField !== 'SpecifiedTenantId') {
                        const dataSource = focusInEvent.component.instance().option('dataSource')
                        focusInEvent.component.instance().option('dataSource', [])
                        focusInEvent.component.instance().option('dataSource', dataSource)
                    }
                }
            }
        }
    }


    /**
     * 如果欄位存在SpecifiedTenantId，重新指定選單的dataSource，以便讀取SpecifiedTenantId。
     * 
     * @param grid 
     */
    dataSourceChangeForSpecifiedTenantId(grid: DxDataGridComponent) {
        grid.instance.on('editorPreparing', (e: EditorPreparingEvent) => {
            const columns = e.component.option('columns') as Column[]
            const specifiedTenantIdColumn = columns.find(c => c.dataField === 'SpecifiedTenantId')
            
            if (specifiedTenantIdColumn && e.dataField !== 'SpecifiedTenantId') {
                const onFocusIn = e.editorOptions?.onFocusIn;
                e.editorOptions = {
                    ...e.editorOptions,
                    onFocusIn(focusInEvent: FocusInEvent){
                        onFocusIn?.apply(this,arguments);
                        const dataSource = focusInEvent.component.instance().option('dataSource')
                        focusInEvent.component.instance().option('dataSource', [])
                        focusInEvent.component.instance().option('dataSource', dataSource)
                    }
                }
            }
        })
    }

    checkTenantIdExist(form: DxFormComponent) {
        const formData = form.instance.option('formData');
        return formData['SpecifiedTenantId'] === undefined ? false: true 
    }
}