import { Directive } from '@angular/core';
import { DxTreeViewComponent } from 'devextreme-angular/ui/tree-view';

@Directive({
    selector: '[appTreeView]',
    standalone: true
})
/**
 * A directive that sets up the common config for dx-tree-view.
 */
export class TreeViewDirective {
    constructor(private treeView: DxTreeViewComponent) {
        this.treeView.hoverStateEnabled = false
        this.treeView.focusStateEnabled = false
        this.treeView.activeStateEnabled = false
    }
}